@import "../../../base";

.landing-splash-container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 100px;

  @media screen and (min-width: 992px) {
    margin-top: 0;
    margin-bottom: 0;
    width: 59%;
    .slick-dots {
      transform: translateX(60px);
    }
  }
  @media screen and (min-width: 1200px) {
    width: 66%;
  }
  @media screen and (min-width: 1600px) {
    width: 63%;
  }

  .slick-slider {
    width: 100%;
  }

  
  .landing-splash-slide-container {
    padding: 0 15px 30px calc(100% - 900px);
    @media screen and (max-width: 991px) {
      padding: 0 15px;
    }

    .landing-splash-slide-inner {
      min-height: 436px;
      display: flex;
      align-items: center;
    }

    .desc-container {
      display: flex;
      flex-direction: column;
      justify-content: center;

      @media screen and (max-width: 991px) {
        align-items: center;
        text-align: center;
        padding: 0 15px 15px 15px;
        max-width: 400px;
        margin: auto;
      }

      h2 {
        font-weight: $font-weight-heavy;
        font-size: $font-size-extra-big;
        white-space: pre-wrap;

        @media screen and (max-width: 991px) {
          font-size: $font-size-big;
        }
      }

      p {
        font-size: $font-size-regular;
        @media screen and (max-width: 991px) {
          font-size: $font-size-small;
        }
      }
    }

    .splash-img-container {
      @media screen and (max-width: 991px) {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0 15px;
      }

      .splash-img {
        max-width: 400px;
        transition: transform 0.3s ease;
        transform: scale(0.95);

        @media screen and (max-width: 991px) {
          max-width: 300px;
        }
    
        img {
          width: 100%;
          height: auto;
        }

        &:hover {
          transform: scale(1);
        }
        &:active {
          transform: scale(0.95);
        }
      }
    }
  }

  .type-2 {
    .splash-img {
      img {
        max-width: 250px;
      }
    }

    .button-container {
      .button-col {
        &:nth-of-type(1) {
          padding-right: 5px;
          margin-bottom: 10px;
        }
        &:nth-of-type(2) {
          padding-left: 5px;
          margin-bottom: 10px;
        }
      }
    }

    @media screen and (min-width: 991px) {
      .splash-img {
        position: absolute;
        right: 15px;
        top: 0;

        img {
          @media screen and (min-width: 1200px) {
            max-width: 350px;
          }
        }
      }
  
      .desc-container {
        min-height: 396px;
        justify-content: flex-end;
  
        p {
          max-width: 400px;
        }
  
        .button-container {
          margin-right: auto;
        }
      }
    }
  }

  .type-3 {
    h1 {
      font-family: $font-after-night;
      font-size: $font-size-extra-big * 0.3 + $font-size-extra-tiny * 2;
      transform: translateX(68px) translateY(-41px);
      @media screen and (max-width: 991px) {
        text-align: center;
        transform: none;
      }
    }

    .easier {
      color: $color-hover;
    }

    .splash-img {
      max-width: 500px;
      margin: auto;
      img {
       
        width: 100%;
        height: auto;
        
        transform: translateX(-45px);
        @media screen and (max-width: 991px) {
          transform: none;
        }
      }
    }
  }
}